<template>
    <div class="container space-block">
        <h1>七夕快乐，我的宝贝！</h1>
        <div class="semi-space-block"></div>
        <p>在这个银河倾泻的夜晚，我愿化作鹊桥，与你相会。</p>
        <div class="semi-space-block"></div>
        <button @click="toggleMessage">点击这里，听听我的心声</button>
        <div class="message" v-if="showMessage">
            <p class="semi-space-block">遇见你，是最美的奇迹。</p>
            <p>愿与你共度每一个七夕，直到永恒。</p>
            <p class="semi-space-block">{{ boyName }} 和 {{ girlName }} 要永远在一起~</p>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
import { useRoute } from 'vue-router';

export default {
    setup() {
        const route = useRoute()
        const boyName = ref(route.query.boyName || "男孩")
        const girlName = ref(route.query.girlName || "女孩")
        const showMessage = ref(false)

        function toggleMessage() {
            showMessage.value = !showMessage.value
        }

        return { boyName, girlName, showMessage, toggleMessage }
    }
}
</script>

<style scoped>

.container {
    margin: auto;
    text-align: center;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    width: 500px;
}

.message {
    margin-top: 20px;
    font-size: 18px;
    color: #333;
}

button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #5c67f2;
    color: white;
}

button:hover {
    background-color: #4a55e6;
}
</style>